.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	will-change: transform;
	padding: 5.6rem var(--gl-indent) 1rem;

	background-color: #2c2c2c;
	font-size: 1rem;

	color: $white;
}

.footer__logo {
	position: relative;
	z-index: 100;

	display: block;

	width: 14.5rem;
	height: 9.4rem;

	@include bp($point_2, min) {
		margin: 0 3rem 3rem 0;
	}
	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.footer__in {
	max-width: 121rem;
	width: 100%;
	margin: 0 auto;
}

.footer__top {
	&:not(:last-child) {
		padding-bottom: 6rem;
	}

	@include bp($point_2, min) {
		@include flex_block(row, wrap, space-between, stretch, stretch);
	}
	@include bp($point_3) {
		@include flex_block(column, wrap, center, center, center);
	}
}

.footer__list_contact,
.footer__list_info {
	@include flex_block(column, wrap, flex-start, stretch, stretch);
}

.footer__list_title {
	font-size: 1.6rem;
	// text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}
}

.footer__info_container_w {
	max-width: 96.5rem;
	width: 100%;
}

.footer__info_container {
	margin: 0 -3rem -3rem 0;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);
	}
	@include bp($point_3) {
		@include flex_block(column, wrap, center, center, center);
	}
}

.footer__info_item {
	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.footer__list_info_w,
.footer__list_contact_w,
.footer__list_social_w {
	padding: 0 3rem 3rem 0;

	@include bp($point_3) {
		@include flex_block(column, wrap, center, center, center);

		text-align: center;
	}
}

.footer__list_contact_w {
	@include bp($point_2, min) {
		width: 42%;
	}
	@include bp($point_3) {
		width: 100%;
	}
}

.footer__list_info_w {
	@include bp($point_2, min) {
		width: 26%;
	}
	@include bp($point_3) {
		width: 100%;
	}
}

.footer__contact_item {
	position: relative;

	width: 100%;

	@include bp($point_2, min) {
		max-width: 30rem;
		padding-left: 3.4rem;
	}
}

.footer__info_link,
.footer__link {
	color: $white;
	transition: opacity ease 0.3s;
	font-weight: 500;
	line-height: 171%;
	letter-spacing: 0.02em;
	font-size: 1.4rem;

	&:hover {
		text-decoration: none;
		opacity: 0.7;
	}
}

.footer__contact_item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3rem;
		}
		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}

	&.footer__contact_item--location {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 5.8rem;
			}
		}
	}
}

.footer__contact_icon {
	width: 1.5rem;
	height: 2rem;
	@include bp($point_2, min) {
		display: block;
		position: absolute;
		top: 0.2rem;
		left: 0;
	}
	@include bp($point_3) {
		position: relative;
		top: 0.6rem;

		display: inline-block;
		margin-right: 1rem;
	}
}

.footer__list_social {
	@include flex_block(row, wrap, flex-start, center, center);

	margin: 0 -3rem -2rem 0;
}

.footer__social_item {
	padding: 0 3rem 2rem 0;
}

.footer__social_link {
	transition: opacity ease 0.3s;

	&:hover {
		opacity: 0.7;
	}
}

.footer__social_link_icon {
	display: block;
	width: 2rem;
	height: 2rem;
}

.footer__bottom {
	position: relative;
	padding: 3rem 0 1.6rem;
	text-align: center;
	color: #c8c8c880;
	font-size: 1.2rem;

	&:after {
		position: absolute;
		top: 0;
		left: 50%;
		display: block;
		width: 200vw;
		height: 1px;
		background-color: rgba($white, 0.1);
		transform: translate(-50%);
		content: '';
	}
}
