:root {
  --full-screen: calc(var(--vh, 1vh) * 100);
  --height-header: 10rem;
  --height-footer: 10rem;
  --gl-indent: 2rem;
}

html {
  height: 100%;
  line-height: 1.33;
  font-family: "Hoves", sans-serif;
  font-weight: 400;
  font: 400 0.5208333333vw/1.33 "Hoves", sans-serif;
  font-size: 10px;
}
@media screen and (min-width: 1441px) {
  html {
    font-size: 0.6944444444vw;
  }
}
@media (max-width: 1440px) and (min-width: 375px) {
  html {
    font-size: 10px;
  }
}
@media screen and (max-width: 374px) {
  html {
    font-size: 2.6666666667vw;
  }
}

body {
  position: relative;
  display: flex;
  height: 100%;
  font-size: 1.6rem;
  color: #333333;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}
@supports (display: grid) {
  body {
    display: block;
  }
}
body.body--open_menu_state {
  overflow: hidden;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  opacity: 1;
  transition: opacity 0.3s;
  z-index: 1000;
}
.body--loaded .preloader {
  pointer-events: none;
  opacity: 0;
}

.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: auto !important;
  overflow-x: hidden;
}

.base {
  width: 100%;
  position: relative;
  padding: var(--height-header) 0 0;
  flex: 1 0 auto;
}

a {
  text-decoration: none;
  color: #4aa3df;
}
a:hover, a:focus {
  text-decoration: underline;
}

.editor-styles-wrapper .wp-block {
  margin-bottom: 5rem;
}

html :where(.wp-block) {
  max-width: 1200px;
}

.editor-styles-wrapper {
  overflow-x: hidden;
}

.header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  height: var(--height-header);
  width: 100%;
  padding: 0 var(--gl-indent);
  font-size: 1rem;
  will-change: transform;
  transition: transform ease 0.5s;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.95);
}
.header.header--pos_state {
  transform: translateY(-100%);
}
.body--open_menu_state .header.header--pos_state {
  transform: translateY(0%);
}

.header__in {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  max-width: 117rem;
  width: 100%;
  margin: 0 auto;
}

.header__logo {
  position: relative;
  z-index: 100;
  display: block;
  width: 7rem;
  height: 4.4rem;
}

.header__logo_img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__nav_w {
  margin-left: auto;
}
@media screen and (min-width: 1024px) {
  .header__nav_w {
    position: relative;
    top: 0.6rem;
    width: 66%;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__nav_w {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: var(--full-screen);
    padding: 8rem 2rem 2rem;
    background-color: #fff;
    overflow: hidden;
    transition: opacity ease 0.4s, transform ease 0.4s;
    transform: translateY(0);
    will-change: transform;
  }
  body:not(.body--open_menu_state) .header__nav_w {
    transform: translateX(100%);
    pointer-events: none;
  }
}

@media screen and (max-width: 1023.5px) {
  .header__nav {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;
  }
}

@media screen and (min-width: 1024px) {
  .header__menu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: center;
    align-items: center;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__menu {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    overflow-y: auto;
    height: 100%;
    width: 100%;
  }
}

@media screen and (min-width: 1024px) {
  .header__menu_item {
    margin-bottom: 1rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__menu_item {
    margin-bottom: 2rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__menu_item:not(:last-child) {
    margin-right: 5.8rem;
  }
}

.header__item_link {
  position: relative;
  line-height: 2;
  color: #333333;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
@media screen and (min-width: 1024px) {
  .header__item_link {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__item_link {
    font-size: 1.6rem;
  }
}
.header__item_link:hover, .header__item_link:focus {
  text-decoration: none;
}
.header__item_link:hover:before, .header__item_link:hover:after, .header__item_link:focus:before, .header__item_link:focus:after {
  opacity: 1;
}
.header__item_link:before, .header__item_link:after {
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  opacity: 0;
  transition: opacity ease 0.3s;
  background-color: #000;
  content: "";
}
.header__item_link:after {
  bottom: -0.2rem;
}
.header__item_link:before {
  top: -0.2rem;
}
.header__item_link.header__item_link--state:before, .header__item_link.header__item_link--state:after {
  opacity: 1;
}

.header__menu_trigger {
  position: relative;
  z-index: 5;
  margin-left: auto;
  width: 3rem;
  height: 2rem;
  cursor: pointer;
}
.header__menu_trigger::before, .header__menu_trigger::after,
.header__menu_trigger span {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 1px;
  transition: transform 0.3s ease;
  will-change: transform;
  background-color: #000;
}
.header__menu_trigger::before {
  content: "";
  transform: translateY(-1rem);
}
.header__menu_trigger::after {
  content: "";
  transform: translateY(1rem);
}
.body--open_menu_state .header__menu_trigger span {
  transform: scale(0);
}
.body--open_menu_state .header__menu_trigger::before {
  transform: rotate(-45deg);
}
.body--open_menu_state .header__menu_trigger::after {
  transform: rotate(45deg);
}
@media screen and (min-width: 1024px) {
  .header__menu_trigger {
    display: none;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__menu_trigger {
    display: block;
  }
}

.section {
  position: relative;
  width: 100%;
  font-size: 1rem;
  padding: 5rem var(--gl-indent);
}

.section_in {
  max-width: 117rem;
  width: 100%;
  margin: 0 auto;
}

.footer {
