//------------------------------------------------------------layout
.header {
	@include flex_block(row, wrap, center, center, center);

	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;
	padding: 0 var(--gl-indent);

	font-size: 1rem;

	will-change: transform;
	transition: transform ease 0.5s;

	@include z-index(header);
	background-color: rgba($white, 0.95);

	// &.header--scroll_state {
	// 	background-color: rgba($white_2, 0.95);
	// }

	&.header--pos_state {
		transform: translateY(-100%);
	}

	.body--open_menu_state &.header--pos_state {
		transform: translateY(0%);
	}
}

//------------------------------------------------------------layout###

.header__in {
	@include flex_block(row, wrap, flex-start, center, center);

	max-width: $page_width;
	width: 100%;
	margin: 0 auto;
}

.header__logo {
	position: relative;
	z-index: 100;

	display: block;

	width: 7rem;
	height: 4.4rem;
}

.header__logo_img {
	@include contain_img;
}

.header__nav_w {
	margin-left: auto;

	@include bp($point_2, min) {
		position: relative;
		top: 0.6rem;

		width: 66%;
	}

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;

		width: 100vw;
		height: var(--full-screen);
		// height: 100%;
		padding: 8rem 2rem 2rem;

		background-color: $white;
		overflow: hidden;
		transition: opacity ease 0.4s, transform ease 0.4s;
		transform: translateY(0);
		will-change: transform;

		body:not(.body--open_menu_state) & {
			transform: translateX(100%);
			// opacity: 0;
			pointer-events: none;
		}
	}
}

.header__nav {
	@include bp($point_3) {
		@include flex_block(row, wrap, center, center, center);

		height: 100%;
		overflow: hidden;
	}
}

.header__menu {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
	@include bp($point_3) {
		@include flex_block(column, nowrap, center, center, center);
		overflow-y: auto;

		height: 100%;
		width: 100%;
	}
}

.header__menu_item {
	@include bp($point_2, min) {
		margin-bottom: 1rem;
	}
	@include bp($point_3) {
		margin-bottom: 2rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-right: 5.8rem;
		}
	}
}

.header__item_link {
	position: relative;

	line-height: 2;
	color: $gray;
	text-transform: uppercase;
	letter-spacing: 0.2em;

	@include bp($point_2, min) {
		font-size: 1.2rem;
	}
	@include bp($point_3) {
		font-size: 1.6rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		&:before,
		&:after {
			opacity: 1;
		}
	}

	&:before,
	&:after {
		position: absolute;

		left: 0;

		width: 100%;
		height: 1px;
		opacity: 0;
		transition: opacity ease 0.3s;
		background-color: $black;
		content: '';
	}

	&:after {
		bottom: -0.2rem;
	}

	&:before {
		top: -0.2rem;
	}

	&.header__item_link--state {
		&:before,
		&:after {
			opacity: 1;
		}
	}
}

.header__menu_trigger {
	$burgerHeight: 2rem;

	position: relative;
	z-index: 5;

	margin-left: auto;
	width: 3rem;
	height: $burgerHeight;

	cursor: pointer;

	&::before,
	&::after,
	span {
		position: absolute;
		left: 0;
		top: 50%;

		width: 100%;
		height: 1px;

		transition: transform 0.3s ease;
		will-change: transform;

		background-color: $black;
	}

	&::before {
		content: '';
		transform: translateY(calc($burgerHeight / 2 * -1));
	}

	&::after {
		content: '';
		transform: translateY(calc($burgerHeight / 2));
	}

	.body--open_menu_state & {
		span {
			transform: scale(0);
		}

		&::before {
			transform: rotate(-45deg);
		}

		&::after {
			transform: rotate(45deg);
		}
	}

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: block;
	}
}
