@import 'ScssHelpers/helpers-all.scss';

.slider {
	padding-top: 0.5rem;
}

.slider__w {
	position: relative;
}

.slider__item__row {
	@include flex_block(row, wrap, flex-start, stretch, stretch);
	@include bp($point_2, min) {
	}
	@include bp($point_3) {
		// @include flex_block(row-reverse, wrap, flex-start, stretch, stretch);
		position: relative;
	}
}

.slider__item_col {
	@include bp($point_3) {
		// min-height: calc(var(--full-screen) - var(--height-header));
	}

	&.slider__item_col--info_mod {
		@include bp($point_2, min) {
			@include flex_block(row, wrap, flex-start, flex-end, flex-end);
			position: relative;
			z-index: 1;

			width: 34%;
			padding-bottom: 31.4%;
		}
		@include bp($point_3) {
			position: relative;
			z-index: 1;
			order: 3;

			width: 100%;
		}
	}

	&.slider__item_col--image_mod {
		@include bp($point_2, min) {
			position: relative;
			width: 66%;

			overflow: hidden;
		}
		@include bp($point_3) {
			height: calc(var(--full-screen) - var(--height-header));
			// position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			// height: 100%;
		}
	}
}

.slider__item__img_w {
	position: relative;

	display: block;

	// height: 100%;
	// padding: 53.8%;

	@include bp($point_2, min) {
		width: 100%;
		height: 57.6vw;
	}
	@include bp($point_3) {
		height: 100%;
		width: 100%;
	}
}

.slider__item__img {
	@include cover_img;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
}

.slider__item_btn {
	position: absolute;

	bottom: 0;

	@include bp($point_2, min) {
		left: 100%;
	}
	@include bp($point_3) {
		right: 0;
		bottom: 100%;
	}
}

.slider__item_title {
	width: 100%;

	font-weight: 300;
	color: $gray_2;
	line-height: 1.08;

	strong {
		font-weight: 700;
		color: $black_2;
	}

	@include bp($point_2, min) {
		font-size: 6.4rem;
	}
	@include bp($point_3) {
		padding: 2rem;
		font-size: 3.4rem;
	}
}

.slider__arrow_w {
	@include flex_block(row, wrap, flex-start, center, center);
	position: absolute;
	z-index: 100;

	max-width: 20rem;

	overflow: hidden;
	@include bp($point_2, min) {
		top: 66.6%;
	}
	@include bp($point_3) {
		top: 0;
		right: 0;
	}
}

.slider__arrow {
	border: 1px solid #f2f2f2;
	padding: 2rem 1.6rem;
	background-color: $white;
	cursor: pointer;
	transition: background-color ease 0.3s;

	&.slider_arrow--prev {
		transform: rotate(180deg);

		margin-right: 2rem;
	}

	&:hover {
		background-color: #f9f9f9;
	}
}

.slider__arrow_icon_w {
	display: block;

	width: 2rem;
	height: 1rem;
}

.slider__dots {
	@include flex_block(row, wrap, flex-start, center, center);

	position: absolute;
	top: 83.5% !important;
	left: 0;

	height: 3rem;

	font-size: 2.4rem;
	color: #bdbdbd;

	@include bp($point_3) {
		display: none;
	}

	span {
		position: relative;

		display: inline-block;
		min-width: 3rem;

		&:first-child {
			margin-right: 8rem;

			&:after {
				position: absolute;
				top: 1.6rem;
				right: -6rem;
				display: block;
				width: 4.6rem;
				height: 1px;
				background-color: #bdbdbd;
				transform: rotate(-45deg);
				content: '';
			}
		}
	}
}
