@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&[type='submit'] {
		@include appearance(none);
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_style_blue {
	font-weight: 400;
	color: $black_2;
	background-color: $white;
	transition: background-color 0.3s ease;

	@include st_btn;

	&:hover {
		background-color: $black_2;
		color: $white;
	}
}

@mixin btn_style_white {
	font-weight: 400;
	color: $black_2;
	background-color: $white;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover {
		background-color: $black_2;
		color: $white;
	}
}

@mixin btn_style_black {
	font-weight: 400;
	color: $white;
	background-color: $black_2;
	transition: background-color 0.3s ease, color ease 0.3s;

	@include st_btn;

	&:hover {
		background-color: $white;
		color: $black_2;
	}
}

//size mixins:
@mixin btn_size_normal {
	height: 7rem;
	padding: 0 2rem;

	line-height: 7rem;
	font-size: 1.2rem;

	min-width: 22rem;
	letter-spacing: 0.2em;
}
